<template><div><h1>样式冗余</h1>
<p>调试时有多个重复样式是正常的，因为不同组件引用了同样的less源码，而调试时是直接把不同组件样式用<code>&lt;style&gt;</code>标签插入页面。</p>
<p>确保在vux-loader里使用<code>duplicate-style</code>来实现对构建css进行压缩。</p>
</div></template><script>
export default {
  head: {"title":" 样式冗余"}
}
  </script>


